import React from "react";
import { useHistory } from "react-router-dom";
const data1 = [
  {
    name: "อ้อยปลูกใหม่",
    detial: "detial1-sugercane",
  },
  {
    name: "อ้อยตอ",
    detial: "detial2-sugercane",
  },
];

const data2 = [
  {
    name: "ข้าวไวต่อแสง",
    detial: "detial3",
  },
  {
    name: "ข้าวไวต่อแสง",
    detial: "detial4",
  },
];
const details =
  "รายละเอียดหน้านี้ การใส่ปุ๋ยครั้งที่ 1 ในระยะเวลาหลังจากข้าวงอก 20-30 วัน ของข้าวไวต่อแสง(นาปี) สำหรับกินเหนียวกับดินร่วนทราย และข้าวไม่ไวต่อแสง(นาปรัง)สำหรับกินเหนียวกับดินร่วนทรายการใส่ปุ๋ยครั้งที่ 2 ในระยะเวลาก่อนข้าวออกดอก 30 วัน ของข้าวไวต่อแสง(นาปี) สำหรับกินเหนียวกับดินร่วนทราย และข้าวไม่ไวต่อแสง(นาปรัง)สำหรับกินเหนียวกับดินร่วนทราย";
function Sugarcane() {
  let history = useHistory();
  return (
    <div className="App bg-light overflow-auto bg-sugar">
      <div className="row">
        <div className="col-4">
          <div className="row">
            {data1.map((index, key) => {
              return (
                <div className="col-12 box-detail" key={key}>
                  <div class="d-grid gap-2 col-12 mx-auto">
                    <button
                      class="btn btn-primary btn-index"
                      type="button"
                      onClick={() => {
                        history.push(index.detial);
                      }}
                    >
                      {index.name}
                    </button>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="textde">
            <button
              class="btn btn-danger bin-back"
              type="button"
              onClick={() => {
                history.push("/");
              }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                class="bi bi-arrow-left-circle-fill"
                viewBox="0 0 16 16"
              >
                <path d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.5 7.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z" />
              </svg>
              &nbsp;กลับ
            </button>
          </div>
          {/* <i class="bi bi-arrow-left-circle-fill"></i> */}
        </div>
        <div className="col-8 bbox">
          <div className="detail-text">
            <h2>รายละเอียด</h2>
            <div className="textde">
              <p>
                <br />
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                การใส่ปุ๋ยอ้อยปลูกใหม่ สำหรับ ดินทราย ดินร่วนทราย ดินเหนียว
                และดินร่วนเหนียว จะใส่ปุ๋ยในช่วงที่อ้อยแตกหน่อได้ 1 เดือน
                การใส่ปุ๋ยอ้อยตอ สำหรับ ดินทราย ดินร่วนทราย ดินเหนียว
                และดินร่วนเหนียว จะใส่ปุ๋ยเมื่อทารตัดตอเสร็จแล้ว
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Sugarcane;
