import React, { useEffect, useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
// import axios from "axios";
// import { api } from "../api.js";
import Keyboard from "react-simple-keyboard";
import "react-simple-keyboard/build/css/index.css";
import Swal from "sweetalert2";
import useChat from "../../useChat";
function Start() {
  let history = useHistory();
  const location = useLocation();
  const str = location.search;
  // const strDetial = location.detail;

  var userID = str.substring(str.indexOf("query") + 6, str.indexOf("&"));
  var subData = str.substring(str.indexOf("&rate") + 6);

  const teatapi = "200-326-333";

  var PreP = userID.substring(userID.indexOf("-") + 1);
  var positionP = PreP.indexOf("-");
  var ResultNn = userID.substring(0, userID.indexOf("-"));
  var ResultPp = PreP.substring(0, PreP.indexOf("-"));
  var ResultKk = PreP.substring(positionP + 1);

  console.log(ResultNn + "," + ResultPp + "," + ResultKk);

  var trueData = "";
  if (subData == "null") {
    trueData = 0;
  } else {
    trueData = subData;
  }
  // var showDataStart = userID.substring(0, 8);
  // const teatapi = "16-0-0"
  // var userID = teatapi.indexOf("-");

  console.log("trueData = ");
  console.log(userID);
  console.log("subData = ");
  console.log(subData);

  const [statusKebord, setStatusKebord] = useState(false);
  const [layoutName, setLayoutName] = useState("default");
  const [input, setInput] = useState("");

  const [thai, setThai] = useState({
    default: [
      "\u005F \u0E45 \u002F \u002D \u0E20 \u0E16 \u0E38 \u0E36 \u0E04 \u0E05 \u0E08 \u0E02 \u0E0A {backspace}",
      // "{tab} \u0E46 \u0E44 \u0E33 \u0E1E \u0E30 \u0E31 \u0E35 \u0E23 \u0E19 \u0E22 \u0E1A \u0E25 \u0E03",
      // "{capslock} \u0E1F \u0E2B \u0E01 \u0E14 \u0E40 \u0E49 \u0E48 \u0E32 \u0E2A \u0E27 \u0E07 {enter}",
      // "{shiftleft} \u0E1C \u0E1B \u0E41 \u0E2D \u0E34 \u0E37 \u0E17 \u0E21 \u0E43 \u0E1D {shiftright}",
      // "@ {space}",
    ],
    shift: [
      // "% + \u0E51 \u0E52 \u0E53 \u0E54 \u0E39 \u0E3F \u0E55 \u0E56 \u0E57 \u0E58 \u0E59 {backspace}",
      // "{tab} \u0E50 \u0022 \u0E0E \u0E11 \u0E18 \u0E4D \u0E4A \u0E13 \u0E2F \u0E0D \u0E10 \u002C \u0E05",
      // "{capslock} \u0E24 \u0E06 \u0E0F \u0E42 \u0E0C \u0E47 \u0E4B \u0E29 \u0E28 \u0E0B \u002E {enter}",
      // "{shiftleft} ( ) \u0E09 \u0E2E \u0E3A \u0E4C \u003F \u0E12 \u0E2C \u0E26 {shiftright}",
      // "@ {space}",
    ],
  });

  const [en, setEn] = useState({
    default: [
      "1 2 3 4 5 6 7 8 9 0 {backspace}",
      // "{tab} q w e r t y u i o p [ ] \\",
      // "{capslock} a s d f g h j k l ; ' {enter}",
      // "{shiftleft} z x c v b n m , . / {shiftright}",
      // "@ {space}",
    ],
    shift: [
      // "~ ! @ # $ % ^ & * ( ) _ + {backspace}",
      // "{tab} Q W E R T Y U I O P { } |",
      // '{capslock} A S D F G H J K L : " {enter}',
      // "{shiftleft} Z X C V B N M < > ? {shiftright}",
      // "@ {space}",
    ],
  });

  const [lang, setLang] = useState(en);

  const keyboardOptions = {
    onChange: (input) => onChange(input),
    onKeyPress: (button) => onKeyPress(button),
    theme: "simple-keyboard hg-theme-default hg-layout-default",
    physicalKeyboardHighlight: true,
    syncInstanceInputs: true,
    mergeDisplay: true,
    debug: false,
    layout: lang,
    display: {
      "{escape}": "esc ⎋",
      "{tab}": "Tab ⇥",
      "{backspace}": "Back ⌫",
      "{enter}": "Enter ↵",
      "{capslock}": "⌨",
      "{shiftleft}": "Shift ⇧",
      "{shiftright}": "Shift ⇧",
    },
  };

  const onChange = (input) => {
    setInput(input);
    // console.log("Input changed", input);
  };

  const onKeyPress = (button) => {
    // console.log("Button pressed", button);
    if (
      button === "{shift}" ||
      button === "{shiftleft}" ||
      button === "{shiftright}"
    ) {
      layoutName == "default"
        ? setLayoutName("shift")
        : setLayoutName("default");
    }
    if (button === "{capslock}") {
      lang == en ? setLang(thai) : setLang(en);
    } else {
      setInput(...input, button);
    }
  };
  console.log("input.length = ");
  console.log(input.length);

  const start_now = () => {
    if (input.length <= 0) {
      Swal.fire({
        position: "center",
        icon: "error",
        title: "กรุณากรอกข้อมูล",
        showConfirmButton: false,
        timer: 1500,
      });
    } else {
      const inputOptions = new Promise((resolve) => {
        const calculus = cal(ResultNn, ResultPp, ResultKk, input);
        const N = calculus.N;
        const P = calculus.P;
        const K = calculus.K;
        // console.log("#" + N + "-" + P + "-" + K + "-@" + "\n")
        sendMessage("#" + N + "-" + P + "-" + K + "-@" + "\n");
        setTimeout(() => {
          Swal.fire({
            position: "center",
            icon: "success",
            title: "เริ่มต้นทำงาน",
            showConfirmButton: false,
            timer: 5000,
          }).then(() => {
            history.push("/");
          });
        }, 1000);
      });

      Swal.fire({
        title: "กำลังถ่ายโอนข้อมูล",
        input: "radio",
        inputOptions: inputOptions,
        inputValidator: (value) => {
          if (!value) {
          }
        },
      });

      // Swal.fire({
      //   position: "center",
      //   icon: "success",
      //   title: "เริ่มต้นทำงาน",
      //   showConfirmButton: false,
      //   timer: 1500,
      // }).then(() => {
      //   console.log("#" + calculus.N + "-" + calculus.P + "-" + calculus.K + "-@" + "\n")
      //   sendMessage("#" + calculus.N + "-" + calculus.P + "-" + calculus.K + "-@" + "\n");
      //   // history.push("/");
      // });
    }
  };

  console.log("sent = ");
  console.log("#" + userID + "-" + input + "-" + trueData + "-@" + "\n");
  const roomId = "1234";

  const { messages, sendMessage } = useChat(roomId);
  const [newMessage, setNewMessage] = React.useState("");
  const handleNewMessageChange = (event) => {
    setNewMessage(event.target.value);
  };

  const handleSendMessage = () => {
    sendMessage(newMessage + "\n");
    setNewMessage("");
  };

  useEffect(() => {
    console.log(messages);
  }, [messages]);

  const cal = (n, p, k, w) => {
    const fixN = [46, 0, 0];
    const fixP = [18, 46, 0];
    const fixK = [0, 0, 60];

    const xP = (100 * p) / fixP[1];
    const N_in_P = (n * xP) / 100;
    const xN = (100 * (n - N_in_P)) / fixN[0];
    const xK = (100 * k) / fixK[2];

    const resultN = (w * xN) / 100;
    const resultP = (w * xP) / 100;
    const resultK = (w * xK) / 100;

    return {
      N: resultN.toFixed(2),
      P: resultP.toFixed(2),
      K: resultK.toFixed(2),
    };
  };

  return (
    <div className="App bg-light overflow-auto bg-app">
      <div>
        <div
          onClick={() => {
            setStatusKebord(false);
          }}
        >
          <br />
          <br />
          <br />
          <br />
          <h2>{userID}</h2>
          <br />
        </div>
        <div className="row textde text-end">
          <div
            className="col"
            onClick={() => {
              setStatusKebord(false);
            }}
          ></div>

          <div className="col-5 textde">
            <input
              type="text"
              className="form-control"
              id="colFormLabel"
              value={input}
              onChange={onChange}
              onClick={() => {
                setStatusKebord(true);
              }}
            />
          </div>

          <div
            className="col textde"
            onClick={() => {
              setStatusKebord(false);
            }}
          ></div>
        </div>
        {statusKebord ? (
          <div className="box-keyborad">
            <br />
            <Keyboard layoutName={layoutName} {...keyboardOptions} />
            <br />
          </div>
        ) : (
          <></>
        )}
        <div
          onClick={() => {
            setStatusKebord(false);
          }}
        >
          <br />
          <br />
          <div className="row text-center">
            <div className="col-2"></div>
            <div class="col-4">
              <button
                type="submit"
                class="btn btn-primary form-control"
                onClick={() => {
                  history.push("/");
                }}
              >
                หน้าหลัก
              </button>
            </div>
            <div class="col-4">
              <button
                type="submit"
                class="btn btn-success form-control"
                onClick={() => {
                  start_now();
                }}
              >
                เริ่ม
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Start;
