import React, { Component, useState, useEffect, useContext } from "react";
import { render } from "react-dom";
import Keyboard from "react-simple-keyboard";
import "react-simple-keyboard/build/css/index.css";
import { inputkeybord } from "../sent/Insert_sent";
import KeybordTest from "./KeybordTest";
// import "./index.css";

function KeybordP() {
  const { textP, setTextP, inputKey2, statusKeyboard2,statuskey } =
    useContext(inputkeybord);
  const [layoutName, setLayoutName] = useState("default");
  const [input, setInput] = useState("");
  const [thai, setThai] = useState({
    default: [
      "\u005F \u0E45 \u002F \u002D \u0E20 \u0E16 \u0E38 \u0E36 \u0E04 \u0E05 \u0E08 \u0E02 \u0E0A {backspace}",
      // "{tab} \u0E46 \u0E44 \u0E33 \u0E1E \u0E30 \u0E31 \u0E35 \u0E23 \u0E19 \u0E22 \u0E1A \u0E25 \u0E03",
      // "{capslock} \u0E1F \u0E2B \u0E01 \u0E14 \u0E40 \u0E49 \u0E48 \u0E32 \u0E2A \u0E27 \u0E07 {enter}",
      // "{shiftleft} \u0E1C \u0E1B \u0E41 \u0E2D \u0E34 \u0E37 \u0E17 \u0E21 \u0E43 \u0E1D {shiftright}",
      // "@ {space}",
    ],
    shift: [
      // "% + \u0E51 \u0E52 \u0E53 \u0E54 \u0E39 \u0E3F \u0E55 \u0E56 \u0E57 \u0E58 \u0E59 {backspace}",
      // "{tab} \u0E50 \u0022 \u0E0E \u0E11 \u0E18 \u0E4D \u0E4A \u0E13 \u0E2F \u0E0D \u0E10 \u002C \u0E05",
      // "{capslock} \u0E24 \u0E06 \u0E0F \u0E42 \u0E0C \u0E47 \u0E4B \u0E29 \u0E28 \u0E0B \u002E {enter}",
      // "{shiftleft} ( ) \u0E09 \u0E2E \u0E3A \u0E4C \u003F \u0E12 \u0E2C \u0E26 {shiftright}",
      // "@ {space}",
    ],
  });

  const [en, setEn] = useState({
    default: [
      "1 2 3 4 5 6 7 8 9 0 {backspace}",
      // "{tab} q w e r t y u i o p [ ] \\",
      // "{capslock} a s d f g h j k l ; ' {enter}",
      // "{shiftleft} z x c v b n m , . / {shiftright}",
      // "@ {space}",
    ],
    shift: [
      // "~ ! @ # $ % ^ & * ( ) _ + {backspace}",
      // "{tab} Q W E R T Y U I O P { } |",
      // '{capslock} A S D F G H J K L : " {enter}',
      // "{shiftleft} Z X C V B N M < > ? {shiftright}",
      // "@ {space}",
    ],
  });
  const [lang, setLang] = useState(en);

  const keyboardOptions = {
    onChange: (input) => onChange(input),
    onKeyPress: (button) => onKeyPress(button),
    theme: "simple-keyboard hg-theme-default hg-layout-default",
    physicalKeyboardHighlight: true,
    syncInstanceInputs: true,
    mergeDisplay: true,
    debug: false,
    layout: lang,
    display: {
      "{escape}": "esc ⎋",
      "{tab}": "Tab ⇥",
      "{backspace}": "Back ⌫",
      "{enter}": "Enter ↵",
      "{capslock}": "⌨",
      "{shiftleft}": "Shift ⇧",
      "{shiftright}": "Shift ⇧",
    },
  };

  const onChange = (input) => {
    setTextP(input);
    // console.log("Input changed", input);
  };

  const onKeyPress = (button) => {
    console.log("Button pressed", button);
    if (
      button === "{shift}" ||
      button === "{shiftleft}" ||
      button === "{shiftright}"
    ) {
      layoutName == "default"
        ? setLayoutName("shift")
        : setLayoutName("default");
    }
    if (button === "{capslock}") {
      lang == en ? setLang(thai) : setLang(en);
    } else {
      setInput(...input, button);
    }
  };

  // const onChangeInput = (event) => {};
  // const [statusKeyboard1, setStatusKeyboard1] = useState(false);

  // const statuskey = () => {
  //   setStatusKeyboard1(false);
  // };

  // const inputKey1 = () => {
  //   setStatusKeyboard1(true);
  // };

  // console.log("statusKeyboard1 = ");
  // console.log(statusKeyboard2);
  return (
    <div>
      <input
        className="form-control"
        value={textP}
        // value={textN}
        placeholder={"P"}
        // onChange={(e) => {
        //   setTextN(e.target.value);
        // }}
        onClick={() => {
          inputKey2();
        }}
      />
      <div className="body-keybord">
        {statusKeyboard2 ? (
          <>
            <br />
            <div className={"keyboardContainer"}>
              
            <button className="btn-down-keybord" onClick={()=>statuskey()}>
              <i class="bi bi-arrow-down-square-fill"></i>&nbsp;Down
            </button>
              <Keyboard layoutName={layoutName} {...keyboardOptions} />
            </div>
          </>
        ) : (
          <></>
        )}
      </div>
    </div>
  );
}

export default KeybordP;
