import React from "react";
import { useHistory } from "react-router-dom";
const detial = [
  {
    id: "5",
    name: "ดินเหนียว",
    num: "5",
  },
  {
    id: "6",
    name: "ดินร่วนเหนียว",
    num: "5",
  },
  {
    id: "7",
    name: "ดินทราย",
    num: "5",
  },
  {
    id: "8",
    name: "ดินร่วนปนทราย",
    num: "5",
  },
];
function Detial2_cassava() {
  let history = useHistory();
  return (
    <div className="App bg-light overflow-auto bg-cassaca">
      <div className="row">
        <div className="col-4">
          <div className="row">
            <div className="col-12 ">
              <p className="title-number">ครั้งที่ 2</p>
            </div>
            {detial.map((index, key) => {
              return (
                <div className="col-12 box-detail" key={key}>
                  <div class="d-grid gap-2 col-12 mx-auto">
                    <button
                      class="btn btn-primary btn-index"
                      type="button"
                      onClick={() => {
                        history.push({
                          pathname: "/sent",
                          search: "?query=" + index.id + "&name=cassava",
                          detail: "?query=ครั้งที่1" + "&name=" + index.name,
                        });
                      }}
                    >
                      {index.name}
                    </button>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="textde ">
            <button
              class="btn btn-danger bin-back"
              type="button"
              onClick={() => {
                history.push("/cassava");
              }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                class="bi bi-arrow-left-circle-fill"
                viewBox="0 0 16 16"
              >
                <path d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.5 7.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z" />
              </svg>
              &nbsp;กลับ
            </button>
          </div>
          {/* <i class="bi bi-arrow-left-circle-fill"></i> */}
        </div>
        <div className="col-8 bbox">
          <div className="detail-text">
            <h2>รายละเอียด</h2>
            <div className="textde">
              <p>
                <br />
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;สำหรับดินทรายใช้สูตร 15-7-18
                ใช้ในอัตรา 100 กิโลกรัม/ไร่ สำหรับดินร่วนทรายใช้สูตร 15-7-18
                ใช้ในอัตรา 100 กิโลกรัม/ไร่ สำหรับดินร่วนเหนียวใช้สูตร 15-7-18
                ใช้ในอัตรา 50 กิโลกรัม/ไร่ และดินเหนียวปนกรวดใช้สูตร 15-15-15
                ใช้ในอัตรา 30-40 กิโลกรัม/ไร่
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Detial2_cassava;
