import React from "react";
import { useHistory } from "react-router-dom";
const data = [
  {
    name: "ข้าวไวต่อแสง",
    num: "5",
  },
  {
    name: "ข้าวไวต่อแสง",
    num: "5",
  },
];
function Item() {
    let history = useHistory();
  return (
    <div className="App bg-light overflow-auto">
      <div className="row">
        <div className="col-4">
          <div className="row">
            <div className="col-12 ">
              <p>ครั้งที่ 1</p>
            </div>
            {data.map((index, key) => {
              return (
                <div className="col-12 box-detail" key={key}>
                  <div class="d-grid gap-2 col-12 mx-auto">
                    <button class="btn btn-primary btn-index" type="button" onClick={()=>{history.push("item-sub")}}>
                      {index.name}
                    </button>
                  </div>
                </div>
              );
            })}

            <div className="col-12 ">
              <p>ครั้งที่ 2</p>
            </div>
            {data.map((index, key) => {
              return (
                <div className="col-12 box-detail" key={key}>
                  <div class="d-grid gap-2 col-12 mx-auto">
                    <button class="btn btn-primary btn-index" type="button">
                      {index.name}
                    </button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        <div className="col-8 bbox">
            <div className="detail-text">

          <p>รายละเอียด</p>
            </div>
        </div>
      </div>
    </div>
  );
}

export default Item;
