import React, { useEffect, useState } from "react";
import Swal from "sweetalert2";

import { useHistory } from "react-router-dom";
import axios from "axios";
import { api } from "../api.js";
const data = [
  {
    name: "ข้าว",
    num: "rice",
  },
  {
    name: "อ้อย",
    num: "sugarcane",
  },
  {
    name: "มันสำปะหลัง",
    num: "cassava",
  },
  {
    name: "ปาล์ม",
    num: "plam",
  },
  {
    name: "ข้าวโพด",
    num: "sweetcorn",
  },
];
function Delete_plant() {
  let history = useHistory();
  const [namePlant, setNamePlant] = useState();
  const [datass, setdatass] = useState([]);
  useEffect(() => {
    axios.get(api + "plantname").then((response) => {
      setdatass(response.data);
    });
  }, []);

  const deleteplant = (id) => {
    Swal.fire({
      title: 'ลบชนิดของพืช',
      icon: 'warning',
      // showDenyButton: true,
      // showCancelButton: true,
      confirmButtonText: `ลบ`,
      showCancelButton: `ยกเลก`,
      timer: 1500
    }).then((result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        Swal.fire('ลบพืชสำเร็จ', '', 'success')
        axios.delete(api + "plantname/" + id).then((res) => {
          history.push("/");
        });
      } 
    })

  };
  
  console.log(namePlant);

  return (
    <div className="App bg-app">
      <div>
        <br />
        <br />
        <br />
        <h2>ลบข้อมูลพืช</h2>
        <br />
      </div>
      <div className="row">
        <div className="col-3"></div>
        <div className="col">
          <select
            class="form-select"
            aria-label="Default select example"
            value={namePlant}
            onChange={(e) => {
              setNamePlant(e.target.value);
            }}
          >
            <option selected>เลือกพืชที่ต้องการจะลบ</option>
            {datass.map((index, key) => {
              return (
                <option key={key} value={index.plant_name}>
                  {index.plant_name}
                </option>
              );
            })}
          </select>
        </div>
        <div className="col-3"></div>
      </div>
      <br />
      <br />
      <div className="row">
        <div className="col"></div>
        <div className="col-3 ">
          <button
            type="button"
            class="btn btn-warning form-control bbtnt"
            onClick={() => {
              deleteplant(namePlant);
            }}
          >
            ลบ
          </button>
        </div>
        <div className="col-3 ">
          <button type="button" class="btn btn-danger form-control bbtnt" onClick={()=>{history.push("/")}}>
            ยกเลิก
          </button>
        </div>
        <div className="col"></div>
      </div>
    </div>
  );
}

export default Delete_plant;
