import React, { useEffect, useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
import axios from "axios";
import { api } from "../api.js";
function Sentnew() {
  let history = useHistory();
  const location = useLocation();
  const str = location.search;
  const strDetial = location.detail;

  var userID = str.substring(str.indexOf("query") + 6, str.indexOf("&"));
  var name = str.substring(str.indexOf("name") + 5, str.length);

  var heads = strDetial.substring(
    strDetial.indexOf("query") + 6,
    strDetial.indexOf("&")
  );
  var subs = strDetial.substring(
    strDetial.indexOf("name") + 5,
    strDetial.length
  );


  var pathBack = "";
  if (name === "sweetcorn" && (userID == "1" || userID == "2" || userID == "3" || userID == "4")) {
    pathBack = "detial1-sweetcon";
  } else if (name === "sweetcorn" && (userID == "5" || userID == "6" || userID == "7" || userID == "8")) {
    pathBack = "detial2-sweetcon";
    /// end_SWEETCORN
  } else if (name === "rice" && (userID == "1" || userID == "2" )) {
    pathBack = "detial1";
  } else if (name === "rice" && (userID == "3" || userID == "4" )) {
    pathBack = "detial2";
  } else if (name === "rice" && (userID == "5" || userID == "6" )) {
    pathBack = "detial3";
  }else if (name === "rice" && (userID == "7" || userID == "8" )) {
    pathBack = "detial4";
    /// end_RICE
  }else if (name === "cassava" && (userID == "1" || userID == "2" || userID == "3" || userID == "4")) {
    pathBack = "detial1-cassava";
  } else if (name === "cassava" && (userID == "5" || userID == "6" || userID == "7" || userID == "8")) {
    pathBack = "detial2-cassava";
    /// end_CASSAVA
  } else if (name === "palm" && (userID == "1" || userID == "2" || userID == "3" || userID == "4")) {
    pathBack = "detial1-plam";
  } else if (name === "palm" && (userID == "5" || userID == "6" || userID == "7" || userID == "8")) {
    pathBack = "detial2-plam";
    /// end_PALM
  } else if (name === "sugarcane" && (userID == "1" || userID == "2" || userID == "3" || userID == "4")) {
    pathBack = "detial1-sugercane";
  } else if (name === "sugarcane" && (userID == "5" || userID == "6" || userID == "7" || userID == "8")) {
    pathBack = "detial2-sugercane";
    /// END_SUGACANE
  } 

  const [datass, setdatass] = useState([]);
  useEffect(() => {
    axios.get(api + name + "/" + userID).then((response) => {
      setdatass(response.data);
    });
  }, []);
  console.log("detial : " + api + name + "/" + userID);
  return (
    <div className="App bg-light overflow-auto">
      <div className="row">
        <div className="col-12">
          <h2>
            {heads} : {subs}
          </h2>
          <br />
        </div>
        {datass.map((index, key) => {
          return (
            <>
              <div className="col-3" key={key}></div>
              <div className="col-6">
                <div className="col-12 box-detail">
                  <div class="d-grid gap-2 col-12 mx-auto">
                    <button class="btn btn-primary btn-index" type="button" onClick={
                      ()=>{
                        history.push({
                          pathname: "/start",
                          search: "?query=" + index.n + "-" + index.p +"-"+index.k,
                        });
                      }
                    }>
                      {index.n}-{index.p}-{index.k}
                    </button>
                  </div>
                </div>
              </div>
              <div className="col-3"></div>
            </>
          );
        })}
        <div className="col-3"></div>
        <div className="col-6">
          <div className="col-12 box-detail">
            <div class="d-grid gap-2 col-12 mx-auto">
              <button
                class="btn btn-success btn-index"
                type="button"
                onClick={() => {
                  history.push({
                    pathname: "/insert-sent",
                    search: "?query=" + userID + "&name=" + name,
                    detail: "?query=" + heads + "&name=" + subs,
                  });
                }}
              >
                เพิ่มสูตร
              </button>
            </div>
          </div>
        </div>
        <div className="col-3"></div>
        <div className="textde ">
          <button
            class="btn btn-danger bin-back"
            type="button"
            onClick={() => {
              history.push(pathBack);
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              fill="currentColor"
              class="bi bi-arrow-left-circle-fill"
              viewBox="0 0 16 16"
            >
              <path d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.5 7.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H11.5z" />
            </svg>
            &nbsp;กลับ
          </button>
        </div>
      </div>
      <div className="row btn-delete">
        <div className="col-10"></div>
        <div className="col">
          <button
            type="button"
            className="btn btn-danger form-control"
            onClick={() => {
              history.push("/delete-formula");
            }}
          >
            ลบ
          </button>
        </div>
      </div>
    </div>
  );
}

export default Sentnew;
