import React from "react";
import { useHistory } from "react-router-dom";
const data = [
  {
    name: "ดินเหนียว",
    num: "5",
  },
  {
    name: "ดินร่วนและดินทราย",
    num: "5",
  },
];
const data1 = [
  {
    name: "ข้าวไวต่อแสง",
    num: "5",
  },
  {
    name: "ข้าวไม่ไวต่อแสง",
    num: "5",
  },
];
function Item_sub() {
  let history = useHistory();
  return (
    <div className="App bg-light overflow-auto">
      <div className="row">
        <div className="col-4">
          <div className="row">
            <div className="col-12 textde">
              <p>ครั้งที่ 1 :</p>
            </div>
            {data.map((index, key) => {
              return (
                <div className="col-12 box-detail" key={key}>
                  <div class="d-grid gap-2 col-12 mx-auto">
                    <button class="btn btn-primary btn-index" type="button">
                      {index.name}
                    </button>
                  </div>
                </div>
              );
            })}
            <div className="col-12 textde">
              <p>ครั้งที่ 2 :</p>
            </div>
            {data.map((index, key) => {
              return (
                <div className="col-12 box-detail" key={key}>
                  <div class="d-grid gap-2 col-12 mx-auto">
                    <button class="btn btn-primary btn-index" type="button">
                      {index.name}
                    </button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        <div className="col-8 bbox">
          <div className="detail-text">
            <p>รายละเอียด</p>
          </div>
        </div>
      </div>
      <div className="row btn-delete">
        <div className="col-10"></div>
        <div className="col">
          <button
            type="button"
            className="btn btn-danger form-control"
            onClick={() => {
              history.push("/delete-formula");
            }}
          >
            ลบ
          </button>
        </div>
      </div>
    </div>
  );
}

export default Item_sub;
