import React, { useEffect, useState } from "react";

import { useHistory } from "react-router-dom";
import axios from "axios";
import { api } from "../api.js";
const data = [
    {
      name: "ข้าว",
      num: "rice",
    },
    {
      name: "อ้อย",
      num: "sugarcane",
    },
    {
      name: "มันสำปะหลัง",
      num: "cassava",
    },
    {
      name: "ปาล์ม",
      num: "plam",
    },
    {
      name: "ข้าวโพด",
      num: "sweetcorn",
    },
  ];
function Delete_formula() {
  const history = useHistory();
  return (
    <div className="App bg-app">
      <div>
        <br />
        <br />
        <br />
        <h2>ลบข้อมูลสูตร</h2>
        <br />
      </div>
      <div className="row">
        <div className="col-3"></div>
        <div className="col">
          <select class="form-select" aria-label="Default select example">
            <option selected>เลือกพืชที่ต้องการจะลบ</option>
            {data.map((index,key)=>{
                return(
                    <option value={index.name}>{index.name}</option>
                )
            })}
          </select>
        </div>
        <div className="col-3"></div>
      </div>

      <br />
      <br />
      <div className="row">
        <div className="col"></div>
        <div className="col-3 ">
          <button type="button" class="btn btn-warning form-control" onClick={()=>{history.push("/")}}>
            ลบ
          </button>
        </div>
        <div className="col-3 ">
          <button type="button" class="btn btn-danger form-control" onClick={()=>{history.push("/")}}>
            ยกเลิก
          </button>
        </div>
        <div className="col"></div>
      </div>
    </div>
  );
}

export default Delete_formula;
