import React, { useEffect, useMemo, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { api } from "../api.js";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import rice from "../../assets/img/logo/rice.png";
import sugarcane from "../../assets/img/logo/sugarcane.png";
import yucca from "../../assets/img/logo/yucca.png";
import plam from "../../assets/img/logo/palm.png";
import corn from "../../assets/img/logo/corn.png";
import plus from "../../assets/img/logo/plus.png";
import wateringplants from "../../assets/img/logo/wateringplants.png";

const data = [
  {
    img: rice,
    name: "ข้าว",
    num: "rice",
  },
  {
    img: sugarcane,
    name: "อ้อย",
    num: "sugarcane",
  },
  {
    img: yucca,
    name: "มันสำปะหลัง",
    num: "cassava",
  },
  {
    img: plam,
    name: "ปาล์ม",
    num: "plam",
  },
  {
    img: corn,
    name: "ข้าวโพด",
    num: "sweetcorn",
  },
];

function Home() {
  let history = useHistory();
  const [datass, setdatass] = useState([]);

  useEffect(() => {
    const interval = setInterval(() => {
      axios.get(api + "plantname").then((response) => {
        setdatass(response.data);
      });
    }, 2000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="App bg-light bg-app">
      <div class="row row-cols-3">
        {data.map((index, key) => {
          return (
            <div class="col box-type">
              <div class="d-grid gap-2 col-12 mx-auto" key={key}>
                <button
                  class="btn btn-primary btn-index"
                  type="button"
                  onClick={() => {
                    history.push("/" + index.num);
                  }}
                >
                  <img
                    src={index.img}
                    style={{ height: 30, marginRight: 10 }}
                  />
                  {index.name}
                </button>
              </div>
            </div>
          );
        })}
        {datass.map((index, key) => {
          return (
            <div class="col box-type">
              <div class="d-grid gap-2 col-12 mx-auto" key={key}>
                <button
                  class="btn btn-primary btn-index"
                  type="button"
                  onClick={() => {
                    history.push({
                      pathname: "/sent-now",
                      search: "?query=" + index.plant_name,
                    });
                  }}
                >
                  <img
                    src={wateringplants}
                    style={{ height: 30, marginRight: 10 }}
                  />
                  {index.plant_name}
                </button>
              </div>
            </div>
          );
        })}
        <div class="col box-type">
          <div class="d-grid gap-2 col-12 mx-auto">
            <button
              class="btn btn-success btn-index"
              type="button"
              onClick={() => {
                history.push("/insert-item");
              }}
            >
              <img src={plus} style={{ height: 30, marginRight: 10 }} />
              เพิ่มชนิดของพืช
            </button>
          </div>
        </div>
      </div>
      
      <div className="row btn-delete">
        <div className="col-10"></div>
        <div className="col">
          <button
            type="button"
            className="btn btn-danger form-control btn-index"
            onClick={() => {
              history.push("/delete-plant");
            }}
          >
            <i class="bi bi-trash"></i>
            &nbsp; ลบ
          </button>
        </div>
      </div>
    </div>
  );
}

export default Home;
